@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.portfolio-header{display:flex;flex-direction:column;justify-content:center}.portfolio-header .title{display:flex;justify-content:center}.portfolio-header .title p{margin:10px 0px -3px 0px;text-shadow:1px 1px 10px rgba(144,144,144,0.5);font-size:20px;font-weight:500}.portfolio-header .subtitle{display:flex;justify-content:center}.portfolio-header .subtitle p{margin:0px;font-weight:400;font-size:12px}.portfolio-header a:link,.portfolio-header a:visited{text-decoration:none;color:black;margin:0px 5px 0px 5px;font-weight:200;font-size:12px;margin:10px 0px -3px 0px;text-shadow:1px 1px 10px rgba(144,144,144,0.5);font-size:20px;font-weight:500}.pdf-viewer{width:100%;height:400px}.contact{display:flex;justify-content:center}.contact .center-container{margin-top:20px;display:flex;flex-flow:column;justify-content:left}.contact .center-container a:link,.contact .center-container a:visited{text-decoration:none;color:black;margin:5px 0px 0px 0px;font-weight:200;font-size:12px;font-weight:400}.contact .center-container p{margin:5px 0px 0px 0px;text-align:left;font-size:12px;font-weight:400}.portfolio-navbar{display:flex;flex-direction:row;justify-content:center}.portfolio-navbar .container{position:relative}.portfolio-navbar .container .options ~ .border{position:absolute;bottom:0;left:0;width:0;height:1px;background-color:#b40000}.portfolio-navbar .container .options:hover ~ .border{width:100%;transition:0.5s}.portfolio-navbar .container .emni-class{color:blue;text-decoration:none;margin:0px 5px 0px 5px;color:black;font-weight:200;font-size:12px}.portfolio-navbar .container .active-class-my{color:#b40000}body{font-family:"Poppins", sans-serif;background-color:#fff;cursor:default}

